import { Component, h, Prop, Element, State, Method } from '@stencil/core';

declare var easyPack: any;
declare const UIkit: any;

@Component({
  tag: 'ks-order-inpost',
})
export class OrderInpost {

    @Prop() search: string = "WrocÅ‚aw, Polska";
    @Prop() api: string = "";
    @Prop() button: string = "";

    @Element() root: HTMLElement;

    widget: any;

    componentDidLoad() {
        this.root.style.display = "block";
    }

    @State() description: string = "";
    @State() address: string = "";
    @State() code: string = "";

    initialized = false;

    Show(event: Event) {
        event.preventDefault();
        const element = document.querySelector("#ks-order-inpost");
        UIkit.modal(element).show();

        if(!this.initialized) {
            (window as any).easyPackAsyncInit = ()=>{
                easyPack.init({
                    defaultLocale: 'pl',
                    mapType: 'osm',
                    searchType: 'osm',
                    points: {
                        types: ['parcel_locker'],
                        functions: ['parcel_collect']
                    },
                    map: {
                        initialTypes: ['parcel_locker']
                    }
                });
    
                this.widget = easyPack.mapWidget("easypack-map", (point) => {
                    this.address = point.name + ", " + point.address["line1"] + ", " + point.address["line2"];
                    this.code = point.name;
                    this.description = point.address["line1"] + ", " + point.address["line2"] + "<br />" + point.name;
        
                    const value = point.address["line1"] + ", " + point.address["line2"] + "  (" + point.name + ")";
                    const punktodbioru = point.name;
        
                    let formData = new FormData();
                        formData.append("rodzaj", "wysylka_inpost");
                        formData.append("koszyk", "0");
                        formData.append("value", value);
                        formData.append("punktodbioru", punktodbioru);
        
                    var headers = new Headers();
                        headers.append('pragma', 'no-cache');
                        headers.append('cache-control', 'no-cache');
        
                    fetch(this.api, {
                        method: 'POST', 
                        headers: headers, 
                        body: formData,
                        credentials: "same-origin"
                    });
        
                    this.Validate();
                    this.Hide();
                });

                this.widget.searchPlace(this.search);
            }

            
            this.initialized = !this.initialized;
        }
    }

    Hide() {
        const element = document.querySelector("#ks-order-inpost");
        UIkit.modal(element).hide();
    }

    @State() valid: boolean = true;

    @Method()
    async Validate() {
        this.valid = this.address != "";
    }

    @Method()
    async IsValid() {
        return this.valid;
    }

    render() {
        return[
            <link rel="stylesheet" href="https://geowidget.easypack24.net/css/easypack.css" />,
            <script async src="https://geowidget.easypack24.net/js/sdk-for-javascript.js"></script>,
            
            <style innerHTML="
                @keyframes ks-fade {
                    from {opacity: 1.0;}
                    to {opacity: 0.4;}
                }

                #easypack-map #listvillages {
                    left: 0 !important;
                    right: 0 !important;
                    top: initial !important;
                }

                #easypack-map .input-group {
                    padding: 0 !important;
                    width: calc(100% - 60px) !important;
                    height: 60px !important;
                }

                #easypack-map input {
                    height: 60px;
                }
                #easypack-map button {
                    height: 60px;
                }
            "></style>,

            <p innerHTML={this.description}></p>,

            <button class={"uk-button uk-width-1-1 uk-margin-top " + (this.valid ? "uk-button-secondary" : "uk-button-danger")}
                    style={{animation: this.valid ? "" : "ks-fade 0.5s cubic-bezier(.86,.01,.67,1) infinite alternate"}}
                    onClick={(e)=>this.Show(e)}>{this.button}</button>,
                    
            <input type="hidden" name="lokalizacjaRuch" value={this.address}/>,
            <input type="hidden" name="ShippingDestinationCode" value={this.code}/>,

            <div id="ks-order-inpost" class="uk-modal-full" uk-modal>
                <div class="uk-modal-dialog">
                    <div class="uk-background-cover uk-flex uk-flex-between uk-flex-column" uk-height-viewport>
                        <div id="easypack-map" style={{height: "100vh", width: "100vw"}}></div>

                        <button class="uk-button uk-button-secondary uk-position-top-right"
                                style={{width: "60px", height: "60px"}}
                                onClick={()=>this.Hide()}>
                            <span uk-icon="close"></span>
                        </button>
                    </div>
                </div>
            </div>
        ]
    }
}